import { useEffect, useState } from "react";


const Test = () =>{
    const [items, setItems] = useState([]);

    useEffect(()=>{
        const dataFetch = async () => {
            const data = await (
                await fetch(
                "https://run.mocky.io/v3/b3bcb9d2-d8e9-43c5-bfb7-0062c85be6f9"
                )
            ).json();

            setItems(data);
            };

        dataFetch();
     },[]);

    return(
        <>
            <ul>
                {items.map((val) => (
                    <li>{val.name}</li>
                ))}
            </ul>
        </>
    )
}

export default Test;